import styled from 'styled-components';

export const DownloadButton = styled.a`
  background: black;
  border: none;
  color: white;
  padding: 15px;
  font-size: 20px;
  max-width: 200px;
  margin: 0 auto;
  &:hover {
    cursor: pointer;
  }
`;



